@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html,
body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #000000;
  background-color: #f2f2f2;
}
.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 250;
}
.hidden {
  display: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (max-width: 320px) {
  .visually-hidden-mobile {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 1200px) {
  .visually-hidden-tablet {
    display: none;
  }
}
@media (min-width: 1200px) {
  .visually-hidden-desktop {
    display: none;
  }
}
/**
  * ===========================================================
  * btn SECTION
  * ===========================================================
  */
.btn {
  display: inline-block;
  padding: 17px 30px 15px 30px;
  color: #ffffff;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  white-space: normal;
  background-color: #81b3d2;
  box-shadow: none;
  border: none;
  cursor: pointer;
  outline: none;
}
.btn:hover {
  background-color: #669ec0;
}
.btn:active {
  color: #88b6d1;
  background-color: #5496bd;
}
.btn:disabled {
  background-color: #e5e5e5;
}
.btn:disabled:hover {
  background-color: #e5e5e5;
}
.btn:disabled:active {
  color: #ffffff;
  background-color: #e5e5e5;
}
/**
  * ===========================================================
  * PAGE HEADER
  * ===========================================================
*/
.page-header {
  width: 320px;
  margin: 0 auto;
  padding: 0;
}
.page-header__banner {
  position: relative;
  height: 388px;
  background-color: #396e98;
  background-image: url("../img/bg-intro-triangle-mobile.svg"), url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-header__banner {
    background-image: url("../img/bg-intro-triangle-mobile.svg"), url("../img/bg-back-mobile@2x.jpg");
  }
}
.page-header__banner--small {
  background-color: #396e98;
  height: 156px;
  background-size: 773px auto;
  background-position: center bottom;
}
.page-header__logo {
  position: relative;
  height: 0;
}
.page-header__top-logo {
  position: absolute;
  top: -57px;
  left: 50%;
  margin-left: -51px;
  width: 102px;
  height: 83px;
  z-index: 100;
}
.page-header__banner-text {
  position: absolute;
  left: 50%;
  margin-left: -230px;
  top: 30px;
  width: 458px;
  height: 78px;
  transform: scale(0.5);
  z-index: 200;
}
.page-header__banner-text--hide {
  display: none;
}
.page-header__banner-text--big {
  position: absolute;
  left: 50%;
  margin-left: -160.5px;
  top: 88px;
  width: 320px;
  height: 215px;
  background-color: transparent;
  z-index: 100;
}
/**
  * ===========================================================
  * PAGE HEADER tablet
  * ===========================================================
*/
@media (min-width: 768px) {
  .page-header {
    width: 768px;
    margin: 0 auto;
  }
  .page-header__banner {
    height: 518px;
    background-image: url("../img/bg-intro-triangle-tablet.svg"), url("../img/bg-back-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain, cover;
  }
  .page-header__banner--small {
    height: 290px;
  }
  .page-header__top-logo {
    position: absolute;
    top: -57px;
    left: 50%;
    margin-left: -51px;
    width: 102px;
    height: 84px;
    z-index: 100;
  }
  .page-header__banner-text--big {
    position: absolute;
    left: 50%;
    margin-left: -230px;
    top: 78px;
    width: 460px;
    height: 350px;
    z-index: 1;
  }
  .page-header__banner-text {
    margin-top: 66px;
    transform: none;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .page-header__banner {
    background-image: url("../img/bg-intro-triangle-tablet.svg"), url("../img/bg-back-tablet@2x.jpg");
  }
}
/**
  * ===========================================================
  * PAGE HEADER desktop
  * ===========================================================
*/
@media (min-width: 1200px) {
  .page-header {
    width: 1200px;
    margin: 0 auto;
  }
  .page-header__banner {
    background-image: url("../img/bg-intro-triangle-desktop.svg"), url("../img/bg-back-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain, cover;
  }
  .page-header__banner--small {
    height: 301px;
  }
  .page-header__top-logo {
    position: absolute;
    top: -57px;
    left: 50%;
    margin-left: -94px;
    width: 187px;
    height: 96px;
    z-index: 100;
  }
  .page-header__banner-text {
    margin-top: 73px;
  }
}
@media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 1.5dppx) {
  .page-header__banner {
    background-image: url("../img/bg-intro-triangle-desktop.svg"), url("../img/bg-back-desktop@2x.jpg");
  }
