/**
  * ===========================================================
  * MAIN NAVIGATION mobile
  * ===========================================================
*/
.main-nav {
  font-size: 18px;
  line-height: 40px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  z-index: 100;
}
.main-nav--nojs .main-nav__btn {
  display: none;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
}
.main-nav__link {
  display: block;
  padding: 8px 20px 6px 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  color: #ffffff;
  background-color: #81b3d2;
  border-top: 1px solid #678fa8;
  border-bottom: 1px solid #9ac2db;
}
.main-nav__link[href]:hover {
  background-color: #669ec0;
  cursor: pointer;
}
.main-nav__link[href]:active {
  background-color: #5496bd;
  color: #88b6d1;
  cursor: pointer;
}
.main-nav__control {
  position: relative;
  min-height: 56px;
  background-color: #ffffff;
}
.main-nav__btn {
  position: relative;
  float: right;
  margin: 5px;
  padding: 0;
  width: 53px;
  height: 46px;
  font-size: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
}
.main-nav__btn-icon {
  position: absolute;
}
.main-nav__btn-icon--menu {
  top: 50%;
  left: 50%;
  fill: #81b3d2;
  width: 23px;
  height: 21px;
  margin-top: -12px;
  margin-left: -12px;
}
.main-nav__btn-icon--cross {
  top: -199px;
  left: 27px;
  fill: #ffffff;
  width: 23px;
  height: 22px;
  margin-top: -13.5px;
  margin-left: -12.5px;
  cursor: pointer;
  display: block;
  z-index: 3;
}
/**
    * ===========================================================
    * MAIN NAVIGATION tablet
    * ===========================================================
*/
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    background-color: #ffffff;
  }
  .main-nav__btn-close {
    display: none;
  }
  .main-nav__item {
    width: 153px;
  }
  .main-nav__item:nth-child(3) {
    margin-left: auto;
  }
  .main-nav__item:first-child .main-nav__link {
    box-shadow: none;
  }
  .main-nav__item:last-child .main-nav__link {
    box-shadow: none;
  }
  .main-nav__link {
    padding: 15px 16px 15px 19px;
    font-size: 14px;
    font-weight: bold;
    line-height: 25px;
    color: #000000;
    background-color: #ffffff;
    box-shadow: none;
    border: none;
  }
  .main-nav__link[href]:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
  .main-nav__link[href]:active {
    background-color: #ffffff;
    color: #b2b2b2;
  }
  .main-nav__link--active {
    color: #766357;
  }
  .main-nav__link--active:hover {
    background-color: #ffffff;
    color: #604e43;
  }
  .main-nav__link--active:active {
    background-color: #ffffff;
    color: #d6d0cc;
  }
  .main-nav__control {
    display: none;
  }
}
/**
  * ===========================================================
  * MAIN NAVIGATION desktop
  * ===========================================================
*/
@media (min-width: 1200px) {
  .main-nav__item {
    width: 239px;
  }
}
