/**
  * ===========================================================
  * ADVANTAGES
  * ===========================================================
  */
.advantages {
  background-color: #eeeeee;
}
.advantages__list {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
}
.advantages__item {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  color: #ffffff;
  font-family: "PT Sans", "Arial", sans-serif;
