}
.recall-form input[type="text"]:focus,
.recall-form input[type="tel"]:focus,
.recall-form input[type="email"]:focus {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 4px #e5e5e5;
}
.recall-form input[type="text"]::placeholder,
.recall-form input[type="tel"]::placeholder,
.recall-form input[type="email"]::placeholder {
  color: #000000;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}
.recall-form input[type="checkbox"],
.recall-form input[type="radio"] {
  position: absolute;
  left: -19px;
}
.recall-form__invalid-msg {
  display: none;
  font-size: 14px;
  line-height: 24px;
  color: #ff5c5c;
  text-transform: none;
  overflow: hidden;
