  margin-bottom: 13px;
  min-height: 56px;
  position: relative;
}
.filter-input-radio + label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background-color: #f2f2f2;
}
.filter-input-radio:checked + label::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  background-color: #333333;
}
.recall-form input[type="radio"] {
  cursor: pointer;
  line-height: 26px;
}
.recall-form input[type="radio"]:hover + .recall-form__radio-control::before {
  background-color: #e5e5e5;
}
.recall-form input[type="radio"]:focus + .recall-form__radio-control::before {
  background-color: #e5e5e5;
  border: 0.5px dashed #81b3d2;
}
.recall-form input[type="radio"]:active + .recall-form__radio-control::before {
  background-color: #d5d5d5;
}
.recall-form input[type="radio"]:disabled + .recall-form__radio-control {
  background-color: #d5d5d5;
}
.recall-form input[type="radio"]:disabled + .recall-form__radio-control::before {
  border-color: #d5d5d5;
}
.recall-form input[type="radio"]:disabled + .recall-form__radio-control::after {
  opacity: 0.3;
}
.recall-form__radio-control {
  display: block;
  padding: 15px 10px;
  padding-left: 75px;
}
/**
    * ===========================================================
    * RECALL FORM visited
    *
    ===========================================================
*/
.recall-form__visited {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  margin-top: 88px;
