  margin-right: 20px;
  margin-bottom: 24px;
  min-height: 56px;
  min-width: 280px;
}
.form-input-checkbox + label::before {
  content: "";
  position: absolute;
  top: 2px;
  left: -4px;
  width: 56px;
  height: 56px;
  border-radius: 5px;
  background-color: #f2f2f2;
}
.form-input-checkbox:checked + label::after {
  content: url("../img/icon-tick.svg");
  position: absolute;
  top: 18px;
  left: 15px;
  width: 25px;
  height: 19px;
}
.recall-form input[type="checkbox"] {
  cursor: pointer;
  line-height: 26px;
}
.recall-form input[type="checkbox"]:hover + .recall-form__checkbox-control::before {
  background-color: #e5e5e5;
}
.recall-form input[type="checkbox"]:focus + .recall-form__checkbox-control::before {
  background-color: #e5e5e5;
  border: 0.5px dashed #81b3d2;
}
.recall-form input[type="checkbox"]:active + .recall-form__checkbox-control::before {
  background-color: #d5d5d5;
}
.recall-form input[type="checkbox"]:disabled + .recall-form__checkbox-control {
  background-color: #d5d5d5;
}
.recall-form input[type="checkbox"]:disabled + .recall-form__checkbox-control::before {
  border-color: #d5d5d5;
}
.recall-form input[type="checkbox"]:disabled + .recall-form__checkbox-control::after {
  opacity: 0.3;
}
