
.adaptive-container() {
  width: @mobile-max-width;
  margin: 0 auto;

  @media @tablet-widths {
    width: @tablet-max-width;
  }

  @media @desktop-widths {
    width: @desktop-max-width;
  }
}

.absolute-top-left(@top: 0, @left: 0, @width: 0, @height: 0) {
  position: absolute;

  top: @top;
  left: @left;

  width: @width;
  height: @height;
}

.absolute-top-right(@top: 0, @right: 0, @width: 0, @height: 0) {
  position: absolute;

  top: @top;
  right: @right;

  width: @width;
  height: @height;
}

.img-x-center(@width) {
  position: absolute;
  left: 50%;
  margin-left: -@width / 2;
}
